import React from 'react';
import '../App.css';
import logo from '../zattire-logo.png';
import FormTemplate from "./formTemplate.jsx"
import questions from "./questions.js"
import Axios from 'axios';


export default class MainPage extends React.Component {

  constructor(props) {
    super();
    this.state = {
      start: false,
      finished: false,
      laoding: false,
      count: 0,
      prev: [],
      priceQuesIndexes: []
    }

    // function binfing
    this.startOnboarding = this.startOnboarding.bind(this);
    this.prevQues = this.prevQues.bind(this)
  }

  // this function  
  startOnboarding(params) {
    this.setState({
      start: true,
      count: 0
    });
  }

  // this will render new questions. 
  renderQuestions() {
    if (this.state.loading)
      return (<p> Loading ...</p>)

    return (
      <FormTemplate
        count={this.state.count + 1}
        nextQues={(data, limit, name) => this.nextQues(data, limit, name)}
        prevQues={() => this.prevQues()}
        questions={questions[this.state.count]}
        limit={questions.length - 1 === this.state.count ? true : false}
      />)
  }

  prevQues() {
    
    if(this.state.prev.length !== 0){
        let newCount = this.state.prev[this.state.prev.length -1]; 
        this.state.prev.splice((this.state.prev.length-1), 1); 
        this.setState({count: newCount, prev: this.state.prev}); 
    }
  }

  async nextQues(data, limit, name) {
    // Answer
    console.log("DATA TO BE SENT", data, "LIMIT", limit);
    if (!data || data === null) {
      this.setState({ loading: false });
      return
    }
    if (data.length === 0) {
      this.setState({ loading: false });
      return
    }
    
    this.state.prev.push(this.state.count);
    this.setState({ loading: true, prev: this.state.prev });

    /* 
      if array has elements greater than 1 then send the array as at is else
      extract data from index[0] and send the data as string. 
    */
    let val = data
    if (data.length === 1) {
      val = data[0]
    }

    try {
      const id = localStorage.getItem("token")
      const dataToSend = {
        [name]: val
      }
      if (!id) {
        const res = await Axios.post("/create-vendor", dataToSend)
        if (res.status === 200) {
          const token = res.data._id
          console.log(token)
          localStorage.setItem("token", token)
        }
      } else {
        const res = await Axios.put(`/update-vendor/${id}`, dataToSend)
        if (res.status === 200) {
          console.log("Done");
        }
      }
      if (this.state.count < questions.length - 1) {
        if (limit) {
          this.setState({ start: false, finished: true });
        }
        let newCount = this.state.count + 1

        if (this.state.count === 10) {
          for(const firstData of data){
            for (let i = this.state.count; i < questions.length; i++) {
              const q = questions[i]
              if (q.question === `${firstData} PRICING`) {
                this.state.priceQuesIndexes.push(i)
                break
              }
            }
          }
          if(this.state.priceQuesIndexes.length > 0){
            const numberOflastQuesLeft = 5
            this.state.priceQuesIndexes.push(questions.length - numberOflastQuesLeft - 1 )
            this.state.priceQuesIndexes.reverse()
          }
        }
        if(this.state.priceQuesIndexes.length > 0){
          newCount = this.state.priceQuesIndexes.pop()
        }
        this.setState({
          count: newCount,
          loading: false
        })
      } else {
        this.setState({ loading: false });
        if (limit) {
          this.setState({ start: false, finished: true });
        }

      }

    } catch (e) {
      console.log(e.message);
      this.setState({ loading: false });
    }
  }

  componentDidMount() {
    localStorage.removeItem("token");
  }

  render() {
    return (
      <div className="App scrolling">
        <header className="App-header">
          <div>
            <img src={logo} className={!this.state.start? "zattire-logo" :"zattire-logo logos"} alt="logo" />
            {this.state.start ?
              <div>

                {this.renderQuestions()}

              </div>
              :
              <div>

                {
                  !this.state.finished ? <p className="thankyou"> An online store locator for fashion designers, make up artists and salons. </p> :
                    <p className="thankyou"> Thank you! Your form has been successfully submited. Our team will connect to you shorty. </p>
                }

                {
                  !this.state.finished ?
                    <div onClick={() => this.startOnboarding()} className="zattire-button">
                      <center>Let's start</center>
                    </div> : ""
                }

              </div>}
          </div>

        </header>

      </div>
    )
  }
}
