import React from 'react';
import "./index.scss";
import { fadeInUp, fadeInDown } from 'react-animations'
import Radium, { StyleRoot } from 'radium';
import arrow from '../arrow.svg';
import enter from '../return.svg';

// Animmation styles.
const styles = {
    fadeInUp: {
        animation: 'x 1s',
        animationName: Radium.keyframes(fadeInUp, 'fadeInUp')
    },
    fadeInDown: {
        animation: 'x 1s',
        animationName: Radium.keyframes(fadeInDown, 'fadeInDown')
    }
}


export default class FormTemplate extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            answer: [],
            allow: true
        }

        // function bindings
        this.storeAnser = this.storeAnser.bind(this);

    }

    // This will hold the data to be sent.  
    storeAnser(ev, type, validate) {
        console.log("FUCKIT", validate)
        // get data from the input. 
        var data = ev.target.value;

        if ((validate === "number-tel" && data.length !== 10) ||(validate === "number-zip" && data.length !== 6)) {
            console.log("reached")
            if (validate === "number-tel" && data.length !== 10) {
                this.setState({ allow: false })
            } else if (validate === "number-zip" && data.length !== 6) {
                this.setState({ allow: false })
            }
        } else {
            this.setState({ allow: true })
            if (type === "checkbox") {
                if (!this.state.answer.includes(data)) this.state.answer.push(data)
                else {
                    for (let index = 0; index < this.state.answer.length; index++) {
                        if (this.state.answer[index] === data) {
                            this.state.answer.splice(index, 1)
                        }
                    }
                }

                // update the state. 
                this.setState({ answer: this.state.answer })
            }

            // update data coming from textfield & radio buttons. 
            else this.setState({ answer: [data] });
        }
    }






    componentDidMount() {
        // set input's focus. 
        document.getElementById("myInput").focus();
    }



    render() {

        return (
            <StyleRoot>
                <div className="formTemplate" style={styles.fadeInDown}>
                    <div className="count-layout"> {this.props.count} <img src={arrow} style={{ marginTop: "-2px" }} alt="arrow" /> </div>
                    <div>
                        <p className="question">  {this.props.questions.question} </p>
                        <form id="questions-xx" onSubmit={(e) => {
                            if(this.state.allow){
                                e.preventDefault();
                                this.props.nextQues(this.state.answer, this.props.limit, this.props.questions.name)
                            }else{
                                e.preventDefault(); 
                            }
                        }}>
                            {
                                // if the question is to answered in textfield or checkbox or radio
                                this.props.questions.type === "string" ||
                                this.props.questions.type === "url" ||
                                    this.props.questions.type === "number" ||
                                    this.props.questions.type === "number-tel" ||
                                    this.props.questions.type === "number-zip" ||
                                    this.props.questions.type === "email" ?
                                    <div>
                                        <input
                                            type={this.props.questions.type === "number-tel" || this.props.questions.type === "number-zip" ? "number" : this.props.questions.type}
                                            id="myInput"
                                            required
                                            onChange={(e) => this.storeAnser(e, "text", this.props.questions.type)}
                                            className="text-input" placeholder="Type Something..."
                                        />
                                        {this.state.allow ? "" : <p> Opps! Seems like you have not entered the information correctly.  </p>}
                                    </div>
                                    :
                                    <div className="define-struct">

                                        {
                                            this.props.questions.options.map((data, index) => {
                                                return (
                                                    <div className="checkboxed">
                                                        <label className="container"> {data}


                                                            <input id="myInput" onChange={(e) => this.storeAnser(e, this.props.questions.type)} type={this.props.questions.type} value={data} name="input" />
                                                            <span className="checkmark"></span>
                                                        </label>
                                                    </div>);
                                            })}
                                    </div>
                            }

                            <div className={this.props.count - 1 === 0 ? "action-buttons2" : "action-buttons"}>
                                {
                                    this.props.count - 1 === 0 ? "" : <p style={{ textAlign: "center" }} onClick={() => this.props.prevQues()}>Prev</p>
                                }

                               {this.state.allow? <input type="submit" className="next-button" value={this.props.limit ? "Submit" : "Next"} />:""}
                                <div className="enter-container">
                                    <p className="enter">press <b>Enter</b></p>
                                    <img className="enter-icon" src={enter} alt="enter-icon" />
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </StyleRoot >)
    }
}